export const World = styled(GiWorld)`
  font-size: 25px;
  color: green;
  margin-right: 8px;
`;

export const Container = styled.div`
  width: 100%;
  height: 100vh;
  overflow: hidden;
  /* background-color: grey; */
  position: relative;
  @media screen and (max-width: 1000px) {
    overflow: unset;
    height: unset;
  }
`;

export const ContainerWrapper = styled.div`
  display: grid;
  grid-template-columns: 40% 60%;
  width: 100%;
  height: 100%;

  @media screen and (max-width: 1000px) {
    display: flex;
    flex-direction: column;
  }

  .left_side_container {
    /* background-color: red; */

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .right_side_container {
    /* background-color: green; */
    padding: 20px;
    height: 88.7%;
    overflow: auto;

    @media screen and (max-width: 1000px) {
      height: unset;
      padding: 10px;
    }

    .brand_name {
      position: relative;
      border: 1px solid none;
      border-radius: 8px;
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 4px;

      .brand_text {
        padding-left: 10px;
        padding-top: 5px;
      }

      .p_1 {
        font-size: 25px;
        font-weight: 500;
        color: #1c39a1;
      }
      .p_2 {
        font-size: 20px;
        font-weight: 350;
      }
    }

    .text {
      border: 1px solid none;
      border-radius: 8px;
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 4px;
      padding: 30px;
      margin-top: 25px;

      @media screen and (max-width: 1000px) {
        padding: 10px;
      }

      .centered_div {
        display: flex;
        justify-content: center;
        h3 {
          font-size: 40px;
          font-weight: 700;
          margin-bottom: 20px;
          color: #1c39a1;
          @media screen and (max-width: 1000px) {
            font-size: 28px;
          }
        }
      }

      .p_3 {
        font-size: 18px;
        color: black;
        font-style: italic;
        font-weight: 350;
      }
    }
    .text_2 {
      border: 1px solid none;
      border-radius: 8px;
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 4px;
      padding: 30px;
      margin-top: 25px;
      @media screen and (max-width: 1000px) {
        padding: 10px;
      }

      .centered_div {
        display: flex;
        justify-content: center;

        h3 {
          font-size: 40px;
          font-weight: 700;
          margin-bottom: 20px;
          text-align: center;
          color: #1c39a1;
          @media screen and (max-width: 1000px) {
            font-size: 28px;
          }
        }
      }
      .only_mobile {
        @media screen and (max-width: 1000px) {
          width: 100%;
          overflow: auto;
          /* box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px; */
          /* background-color:red ; */
        }
      }
      .info_card {
        display: grid;
        grid-template-columns: 50% 50%;

        @media screen and (max-width: 1000px) {
          display: flex;
          flex-direction: row;
        }

        @media screen and (min-width: 1350px) {
          display: grid;
          grid-template-columns: 33% 33% 33%;
        }

        .card_container {
          margin: 15px;

          min-width: 200px;
          border: 1px solid none;
          border-radius: 8px;
          box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 4px;
          z-index: 20;
          transition: 0.3s;

          &:hover {
            box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
          }

          @media screen and (max-width: 1000px) {
            margin: 15px;
          }

          .image {
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }

          .descrip_text {
            padding: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            h4 {
              font-size: 25px;
              font-weight: 400;
            }
            p {
              text-align: center;
            }
          }
        }
      }
    }

    .text_3 {
      border: 1px solid none;
      border-radius: 8px;
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 4px;
      padding: 30px;
      margin-top: 25px;
      @media screen and (max-width: 1000px) {
        padding: 10px;
      }

      .centered_div {
        display: flex;
        justify-content: center;

        h3 {
          font-size: 40px;
          font-weight: 700;
          margin-bottom: 20px;
          text-align: center;
          color: #1c39a1;
          @media screen and (max-width: 1000px) {
            font-size: 28px;
          }
        }
      }

      .country_card {
        display: grid;
        grid-template-columns: 40% 60%;

        @media screen and (max-width: 800px) {
          display: flex;
          flex-direction: column-reverse;
          margin-top: 20px;
          
        }
        .text_side {

          @media screen and (max-width: 800px) {
            display: grid ;
            grid-template-columns: 50% 50% ;
            margin-top: 50px ;
          }

          p {
            display: flex;
            align-items: center;
            margin: 10px;
            font-size: 18px;
            font-weight: 350;
          }
        }

        .image_side {
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
    }
  }
`;
